<template>
	<view>
		<view class="login-main">
			<view class="login-logo">
			</view>
			<view class="">
				<view class="title_tip">
					<text>密码登录</text>
				</view>
				<view class="tel_login">
					<input type="number" maxlength="11" v-model="phone" placeholder="手机号"/>
					<input type="password" v-model="password" placeholder="密码"/>
					<view class="forget_pwd">
						<text @click="forgetpwd">忘记密码？找回密码</text>
					</view>
				</view>
				<button type="default" hover-class="button-hover" class="pwdlogin" @click.stop="pwdlogin">登录</button>
			</view>
		</view>
		<view class="footer">
			<checkbox-group name="" @change="select">
				<label>
					<checkbox :checked="check"/>
					<text>我已阅读并同意</text> <navigator class="user" @click.stop="EnterUseragree">《用户协议》</navigator>
					<text>和</text> 
					<navigator class="secret" @click.stop="EnterSecrete">《隐私政策》</navigator>
				</label>
			</checkbox-group>
			
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				phone:"",
				password:'',
				check:false
			}
		},
		methods: {
			// 密码登陆
			pwdlogin(){
				console.log(this.check)
				console.log(!this.check)
				if(this.phone==''){
					uni.showToast({
						title: '手机号码不能为空',
						icon: "none"
					});
					return;
				}
				if(this.password==''){
					uni.showToast({
						title: '密码不能为空',
						icon: "none"
					});
					return;
				}
				if (!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phone))) {
					uni.showToast({
						title: '请填写正确手机号码',
						icon: "none"
					});
					return;
				}
				if(!this.check){
					uni.showToast({
						title: '请同意用户协议',
						icon: "none"
					});
					return;
				}
				this.$myRequest({
					url:'/token/loginForAndroid',
					method:'POST',
					data:{
						telephone:this.phone,
						password:this.password,
					}
				}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						uni.setStorageSync('token', res.data.object.token);//token
						uni.setStorageSync('userName', res.data.object.name);//用户名称
						uni.setStorageSync('doctorId', res.data.object.doctorId);//用户ID
						if(res.data.object.avatarLinks){
							uni.setStorageSync('avatarUrl', res.data.object.avatarLinks);//用户名称
						}
						var pages=this.$store.state.PagesUrl//页面路径
						console.log(this.$store.state.PagesType)
						if(this.$store.state.PagesType=='1'){
							uni.switchTab({
								url:this.$store.state.PagesUrl,//跳转相应的页面
							})
						}else if(this.$store.state.PagesType=='2'){//跳转相应的页面
							uni.navigateTo({
								url:this.$store.state.PagesUrl
							})
						}
						
					}else{
						uni.showToast({
							title: res.data.msg,
							icon: "none"
						});
					}
				}).catch(err=>{
					console.log(err)
				})
			},
			// 忘记密码
			forgetpwd(){
				
			},
			// 同意隐私政策
			select(e){
				this.check=!this.check
			},
			EnterUseragree(){},
			EnterSecrete(){},
		},
		onHide() {
			clearInterval(this.Timer)//页面隐藏时，清除定时器
		},
		onLoad(options) {
			console.log(this.$store.state.PagesUrl)//从那个页面过来的路径
			console.log(options)
			if(options){
				this.phone=options.telephone
				this.password=options.telephone
			}
		}
	}
</script>

<style>
	.login-main {
		flex: 1;
		padding: 0 70rpx;
	}
	.login-logo {
		width: 248rpx;
		padding-bottom: 50rpx;
		margin: 50rpx auto 0 auto;
	}
	.login-logo image{
		width: 100%;
		height: 100%;
	}	
	.title_tip{
		color: #000000;
		font-size: 42rpx;
		font-weight: 600;
		margin-bottom: 50rpx;
	}
	.tel_login input{
		border-bottom: 1px solid #EEEEEE;
		padding: 30rpx 0;
	}
	.pwdlogin{
		width: 514rpx;
		height: 80rpx;
		line-height: 80rpx;
		margin: 44rpx auto;
		color: #8F8F8F;
		background: #EBEBEB;
		border: 1px solid transparent;
	}
	.forget_pwd{
		color: #333333;
		font-size: 24rpx;
		text-align: right;
		margin-top: 24rpx;
		border-radius: 10rpx;
	}
	.footer{
		display: flex;
		justify-content: center;
		color: #909090;
		font-size: 28rpx;
	}
	.footer .user{
		color: #86B0D4;
	}
	.footer .secret{
		color: #86B0D4;
	}
	.footer .uni-label-pointer{
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: center;
	}
</style>
